import config from '../config';
import { OpenBet } from '../models/BetOrder';
import { Result } from '../models/EventOdd'
import moment from 'moment'
import _ from 'lodash'

import axios from 'axios'
const axiosJsonOdd = axios.create({ headers: {'JsonOdds-API-Key': config.jsonOddApiKey}})

const fetch_jsonOdd_result = async () => {

	try{
		const openBets = await OpenBet.find({})
		if(_.isEmpty(openBets)) next()

		const openBetJsonOddEvents = _.compact(_.uniqBy([].concat.apply([], openBets.map(openBet => {
			return openBet.eventOdds.map(event => {
				if(event.status === 'Pending' && event.source.provider === 'jsonOdd' && moment().isAfter(event.cutOffTime) ){
					return _.pick(event, ['eventOddId', 'sport', 'matchTime', 'details', 'league', 'region', 'status', 'team', 'source', 'oddType'])
				}
				return null
			})
		})), 'eventOddId'))


		console.log('fetch json result' + openBetJsonOddEvents)
		// eventOddId = generate array unique source id and oddtype from all json open bet

		if(!_.isEmpty(openBetJsonOddEvents)){
			await Promise.all(openBetJsonOddEvents.map(async event => {
				const response = await axiosJsonOdd.get(`https://jsonodds.com/api/results/getbyeventid/${event.source.id}?oddtype=${event.oddType}`)
				let oddResult = {}
				if(response.data.length > 0 && response.data.length === 1){
					oddResult = Object.assign({}, response.data[0])
				}else if(response.data.length > 1){
					const response2 = await axiosJsonOdd.get(`https://jsonodds.com/api/results/getbyeventid/${event.source.id}?oddtype=${event.oddType.replace(/\s/g,'')}`)
					if(response2.data.length > 0 && response2.data.length === 1){
						oddResult = Object.assign({}, response2.data[0])
					}
				}else if(_.isEmpty(response.data)){
					console.log(`error error error error error jsonOdd cannot get result from openbet event, response empty ${event.source.id} ${event.oddType}`)
				}

				if(!_.isEmpty(oddResult) && oddResult.Final && oddResult.FinalType !== 'NotFinished'){
					let resultStatus = ''
					let note = ''
					switch(oddResult.FinalType){
						case 'Finished':
							resultStatus = 'Finished'
							break;
						case 'Canceled':
							resultStatus = 'Canceled'
							break;
						case 'Postponed':
							resultStatus = 'Postponed'
							note = 'Postponed = Canceled'
							break;
						default:
							resultStatus = 'Review'
							note = 'Server Reviewing... TBD'
							return
					}

					const newResult = {
						uniqueId: event.uniqueId,
						eventResultId: event.eventOddId,
						source: {
							provider: 'jsonOdd',
							id: event.source.id,
						},
						sport: event.sport,
						oddType: event.oddType,
						league: event.league,
						region: event.region,
						details: event.details,
						matchTime: event.matchTime,
						team: event.team,
						score:{
							homeScore: oddResult.HomeScore || 0,
							awayScore: oddResult.AwayScore || 0
						},
						status: resultStatus,
						note: note,
						expireAt: moment().add(3, 'd')
					}

					const existedResult = await Result.findOne({ uniqueId: event.uniqueId })
					if(_.isEmpty(existedResult)){
						await new Result(newResult).save()
						console.log('saved jsonOdd new result ' + event.uniqueId)
					}

				}else{
					console.log(`json odd event ${event.uniqueId} not finish yet, skip save result`)
				}
		
				return null
			}))
		
		}else{
			console.log('use open bet event id to update json odd result, but open bet empty')
		}
	}catch(err){
		throw err
	}

}

export default fetch_jsonOdd_result
